import styled from "styled-components";
import PageTemplate from "../component/PageTemplate.tsx";
import ImgBanner from "../assets/img/production/banner.png";
import ImgContent01 from "../assets/img/production/product1/content01.png";
import ImgMoContent01 from "../assets/img/production/product1/mo_content01.png";

import { useContext } from "react";
import { WrapperContext } from "../component/WrapperContext.tsx";
const Main = styled.div`
  width: 100%;
  height: 100%;
  text-align: left;
  padding: ${(props) => (!props.isMobile ? "25px 0 30px 10px" : "25px 0 0 0")};
`;

const Content = styled.img`
  width: 100%;
  margin-bottom: 10vh;

  @media screen and (max-width: 750px) {
    margin-bottom: 3vh;
  }
`;

export default function Product1() {
  const { isMobile } = useContext(WrapperContext);

  return (
    <PageTemplate bannerImage={ImgBanner}>
      <Main isMobile={isMobile}>
        <Content src={!isMobile ? ImgContent01 : ImgMoContent01} />
      </Main>
    </PageTemplate>
  );
}
