import styled from "styled-components";
import PageTemplate from "../component/PageTemplate.tsx";
import ImgBanner from "../assets/img/introduction/banner.png";
import ImgContent01 from "../assets/img/introduction/business/content01.png";
import ImgMoContent01 from "../assets/img/introduction/business/mo_content01.png";
import { useContext } from "react";
import { WrapperContext } from "../component/WrapperContext.tsx";
import Square from "../component/product/Square.tsx";

const Main = styled.div`
  width: 100%;
  height: 100%;
  text-align: left;
  padding: ${(props) => (!props.isMobile ? "25px 0 0 10px" : "25px 0 0 0")};
`;

const Content = styled.img`
  width: 100%;
  margin-bottom: 10vh;
`;

export default function Business() {
  const { isMobile } = useContext(WrapperContext);

  return (
    <PageTemplate bannerImage={ImgBanner}>
      <Main isMobile={isMobile}>{!isMobile ? <Content src={ImgContent01} /> : <Content src={ImgMoContent01} />}</Main>
    </PageTemplate>
  );
}
